.header {
    width: 100%;
    display: flex;
    position: relative;
    background-image: linear-gradient(180deg, #000 65%, transparent);
}

.headerInner {
    margin: 0 auto;
    padding: 40px 28px;
    width: 100%;
    max-width: 1170px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    font-family: "Mazzard", sans-serif;
    font-weight: 600;
    font-size: 25px;
    color: #fff;
}

.navigation {
    display: none;
}

.upLine {
    width: 32px;
    height: 2px;
    background-color: #fff;
    margin-bottom: 7px;
}

.underLine {
    width: 32px;
    height: 2px;
    background-color: #fff;
}

.header::before {
    content:"";
    position: absolute;
    width: 376px;
    height: 303px;
    left: -129px;
    top: -135px;
    background: radial-gradient(50% 50% at 50% 50%, rgba(255, 255, 255, 0.09) 0%, rgba(196, 196, 196, 0) 100%);
}

.header::after {
    content: "";
    position: absolute;
    width: 321.54px;
    height: 190.77px;
    left: 35px;
    top: -30px;
    background: linear-gradient(180deg, rgba(0, 65, 218, 0.115) 0%, rgba(40, 0, 153, 0.115) 91.72%);
    filter: blur(80px);
    border-radius: 2.71815e+07px;
    transform: rotate(131.65deg);
}