.tradingWrapper {
    margin-top: 25px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
    position: relative;
    padding-bottom: 272px;
}

h1 {
    text-transform: uppercase;
    font-family: "Mazzard", sans-serif;
    font-size: 14px;
    color: #aaaaaa;
    margin-bottom: 28px;
}

.slogan {
    padding: 0 28px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "Mazzard", sans-serif;
    font-weight: 600;
    font-size: 28px;
    line-height: 61px;
}

.firstWord {
    display: flex;
    justify-content: center;
}

.lastWord {
    color: transparent;
    background-clip: text;
    background-image: linear-gradient(90deg, #6AA9FF, #CB77FF);
}

.textAfterSlogan {
    
    margin-top: 20px;
    padding: 0 84px;
    text-align: center;
    line-height: 25px;
    font-family: "Mazzard", sans-serif;
    font-size: 14px;
    color: #E9E9E9;
}

.learnMoreButton {
    margin-top: 40px;
    width: 190px;
    height: 60px;
    background-color: #1556EE;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "Mazzard", sans-serif;
    font-size: 15px;
    font-weight: 500;
    line-height: 17px;
    color: #fff;
    margin-bottom: 50px;

}

.buttonArrow {
    width: 32px;
    height: 32px;
    background-color: #fff;
    border-radius: 32px;
    margin-left: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.bgScheme {
    width: 100%;
    height: 272px;
    background-image: url(/public/img/bgScheme.svg);
    background-repeat: no-repeat;
    background-position: bottom 0px right -220px;
}